<template>
  <el-dialog
    class="addOrUpdate"
    :title="!dataForm.id ? '校验规则新增' : '校验规则修改'"
    :close-on-click-modal="false"
    top="80px"
    :visible.sync="visible">
    <el-scrollbar style='height: 500px;'>
      <el-form :model="dataForm" :rules="dataRule" ref="dataForm" @keyup.enter.native="dataFormSubmit()" label-width="120px">
        <el-form-item label="校验名称" prop="ruleName">
          <el-input v-model="dataForm.ruleName"></el-input>
        </el-form-item>
        <el-form-item label="校验类型" prop="ruleType">
          <el-select v-model="dataForm.ruleType">
            <el-option v-for="item in ruleType" :label="item.text" :key='item.id' :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="校验对象" prop="ruleCheckobject">
          <el-select v-model="dataForm.ruleCheckobject">
            <el-option v-for="item in ruleCheckobject" :label="item.text" :key='item.id' :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否启用" prop="ruleStatus">
          <el-select v-model="dataForm.ruleStatus">
            <el-option v-for="item in ruleStatus" :label="item.text" :key='item.id' :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="元数据" prop="tabId">
          <el-select v-model="dataForm.tabId">
            <el-option v-for="item in tab" :label="item.tabName" :key='item.tabCode' :value="item.tabCode"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="单人员校验语句信息保存时使用" prop="ruleChecksingle">
          <el-input type="textarea" :rows="5" v-model="dataForm.ruleChecksingle"></el-input>
        </el-form-item>
        <el-form-item label="批量校验语句信息保存时使用" prop="ruleCheckperson">
          <el-input type="textarea" :rows="5" v-model="dataForm.ruleCheckperson"></el-input>
        </el-form-item>
        <el-form-item label="接收校验语句信息保存时使用" prop="ruleCheckreceive">
          <el-input type="textarea" :rows="5" v-model="dataForm.ruleCheckreceive"></el-input>
        </el-form-item>
      </el-form>
    </el-scrollbar>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>

<script>
  export default {
    data () {
      return {
        visible: false,
        sysId:'',
        dataForm: {ruleId:0,ruleName:'',tabId:'',ruleType:0,ruleCheckobject:0,ruleStatus:1,ruleChecksingle:'',ruleCheckperson:'',ruleCheckreceive:''},
        dataRule: {
          ruleName: [
            { required: true, message: '方案名称必填', trigger: 'blur' }
          ],
          ruleType:[
            { required: true, message: '请选择校验类型', trigger: 'change' }
          ],
          ruleCheckobject:[
            { required: true, message: '请选择校验对象', trigger: 'change' }
          ],
          ruleStatus:[
            { required: true, message: '请选择启用', trigger: 'change' }
          ],
          tabId:[
            { required: true, message: '请选择元数据', trigger: 'change' }
          ]
        },
        ruleType:[{id:0,text:'错误'},{id:1,text:'提醒'}],
        ruleCheckobject:[{id:0,text:'人员'},{id:1,text:'机构'}],
        ruleStatus:[{id:0,text:'禁用'},{id:1,text:'启用'}],
        tab:[]
      }
    },
    methods: {
      init(id) {
        this.dataForm.ruleId=id || 0
        this.visible = true;
        this.getMetaData();
        this.$nextTick(() => {
          this.$refs['dataForm'].resetFields()
          if(this.dataForm.ruleId) {
            this.$http({
              url: this.$http.adornUrl(this.API.queryDataChechruleDetails),
              method: 'post',
              params: this.$http.adornParams({
                ruleId:id
              })
            }).then(({data}) => {
              if (data && data.code === 0) {
                this.dataForm.ruleId=data.data.ruleId;
                this.dataForm.ruleName=data.data.ruleName;
                this.dataForm.ruleType=data.data.ruleType?parseInt(data.data.ruleType):0;
                this.dataForm.ruleCheckobject=data.data.ruleCheckobject?parseInt(data.data.ruleCheckobject):0;
                this.dataForm.ruleStatus=data.data.ruleStatus?parseInt(data.data.ruleStatus):1;
                this.dataForm.tabId=data.data.tabId;
                this.dataForm.ruleChecksingle=data.data.ruleChecksingle;
                this.dataForm.ruleCheckperson=data.data.ruleCheckperson;
                this.dataForm.ruleCheckreceive=data.data.ruleCheckreceive;
              }
            })
          }
        })
      },
      getMetaData(){
        this.$http({
          url: this.$http.adornUrl(this.API.getPersonDataTableTree),
          method: 'post',
          data: this.$http.adornData()
        }).then(({data}) => {
          this.treeData=[];
          if(data && data.code===0){
            this.tab=data.data;
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      // 表单提交
      dataFormSubmit () {
        this.$refs['dataForm'].validate((valid) => {
          if (valid) {
            this.dataForm.ruleId=this.dataForm.ruleId || undefined
            this.$http({
              url: this.$http.adornUrl(this.API[!this.dataForm.ruleId?'saveDataCheckrule':'updateDataCheckrule']),
              method:'post',
              data:this.$http.adornData(this.dataForm)
            }).then(({data}) => {
              if(data && data.code===0){
                this.$message({
                  message: '操作成功',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.visible = false
                    this.$emit('refreshDataList')
                  }
                })
              }else{
                this.$message.error(data.msg);
              }
            })
          }
        })
      }
    }
  }
</script>
<style scoped>
  .addOrUpdate /deep/ .el-dialog__body{padding:0 0 0 10px;}
  .addOrUpdate .el-form{padding-right:10px;padding-top: 10px;}
</style>



